.projectPage {
  display: flex;
  justify-content: space-around;
  gap: 20px;
  margin-top: 20px;

  & > div {
    flex-grow: 1;
  }
}

.headerWrapper {
  display: flex;
  justify-content: space-between;
  background-color: var(--main-background-color-alpha)
}

.knowledgeBaseUploadForm {
  position: relative;
  height: 200px;
  padding: 5px;
  border: 5px dashed var(--color-border-main);
  border-radius: 10px;
  overflow-x: hidden;
  overflow-y: auto;

  input {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: #fff;
  }
}

.dragndropMessage {
  position: absolute;
  top: 50%;
  left: 50%;
  white-space: nowrap;
  transform: translate(-50%, -50%);
}

.filesList {
  list-style: none;
  
  li {
    display: flex;
    justify-content: space-between;
  }
}
